.locationImage{
    height: 50px;
    width: 50px;
    margin: 0 auto;

}

.locationImage:hover{
    cursor: pointer;
}
.MapImage{
    height: 50px;
    width: 50px;
}

@media screen and (min-width: 321px){
    .MapImage{
        height: 100px;
        width: 100px;
    }

    .locationImage{
        height: 100px;
        width: 100px;    
        margin: 5px 0 10px 0;
        -ms-flex-item-align: center;
            align-self: center;
    }
}

@media screen and (min-width:600px){
    .locationImage{
        grid-row:2/3;
        grid-column:1/2;
    }
}


