.Numbers{
    display: -ms-flexbox;
    display: flex;
    width: 80%;
    -ms-flex-flow: wrap;
        flex-flow: wrap;
    margin: 0 auto;
}

.Number{
    border: 1px solid black;
    margin:1px;
    border-radius:4px;
    width: 48px;
    height: 48px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    background-color: #326087;
    opacity: .8;
}

.Number:hover{
    cursor: pointer;
    opacity: 1;
}

.Selected{
    background-color:#2C8693;
}

.Selected:hover,
.Taken:hover{
    cursor: not-allowed;
}

.Taken{
    background-color:#F19722;
}

.Title{
    display: block;
    width: 100%;
    color: #326087;
}


