    opacity: 0;
}

.ShowCloseButton {
    opacity: 1;
    transition: opacity 1s;
} */

.PlayerButton .Image {
    display: -ms-flexbox;
    display: flex;
    vertical-align: middle;
    -ms-flex-align: center;
        align-items: center;
    margin-right: 5px;
}

.PlayerButton img {
    width: 50px;
    height: 50px;
    display: -ms-flexbox;
    display: flex;
    margin: 5px;
    left: 0;
    border: 1px solid white;
    border-radius: 50%;
}

.PlayerButton .Name {
    display: -ms-flexbox;
    display: flex;
    padding-top: 3px;
    -ms-flex-positive: 2;
        flex-grow: 2;
    -ms-flex-align: center;
        align-items: center;
}

.PlayerButton:hover{
    cursor:pointer;
}

.PlayerNumber {
    width: 10%;
    text-align: center;
    display: -ms-flexbox;
    display: flex;
    vertical-align: middle;
    color: #C33325;
    background-color: #F0F1D5;
    border-radius: 0 5px 5px 0;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
}

.Red {
    background-color: #C33325;
    border: 1px solid #C33325;
}

.Green {
    background-color: #2C8693;
    border: 1px solid #2C8693;
}

.Avatar {
    margin-left: 5px;
}

/* .HoverOptions {
    width: 300px;
    height: 20px;
    display: inline-block;
} */

@media(min-width: 650px) {
    .PlayerButton {
        width: 300px;
    }
    .HoverOptions {
        width: 300px;
    }
}


