.Tab{
    width: 200px;
    height: 45px;
    border-radius: 5px 5px 0 0;
    border: 1px solid #326087;
    border-bottom: 0;
    margin: 0 5px;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction: column-reverse;
        flex-direction: column-reverse;
    -ms-flex-align: center;
        align-items: center;
    color:#326087;
}

.Tab:hover{
    cursor: pointer;
}

.Selected{
    background-color: #326087;
    color: white;
}


